import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class StartupindiaService {
  api_url = environment.api_url;
  constructor(private http: HttpClient) { }

  async getUserData(token){
    return await this.http.get('https://api.startupindia.gov.in/sih/api/auth/sso/services/get/userDetails',{
      headers:{
        'x-auth-token':token
      }
    }).toPromise();
  }

  async getAdditionalData(token,dpiitNumber){
    return await this.http.post('https://api.startupindia.gov.in/sih/api/auth/sso/services/get/dpiit',
    {
      'dpiitNumber':dpiitNumber
    },
    {
      headers:{
        'x-auth-token':token
    }
    }).toPromise();
  }


}
