import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
import {StartupindiaService} from '../shared/startupindia.service'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  auth_token:any;
  promptMsg:string;
  constructor(
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private _startupIndia:StartupindiaService,
    private router:Router) { 
    this.route.queryParams.subscribe(params=>{
      this.auth_token = params['auth_token'];
    });
  }

  ngOnInit() {
    this.getStartUpIndiaUserData();
  }

  async getStartUpIndiaUserData(){
    try{
      this.promptMsg = 'Fetching from #startupindia...'
      let resp:any = await this._startupIndia.getUserData(this.auth_token);
      if(resp.role == 'Startup'){
        this.router.navigate([`register/via/startup-india/provider/startup/${this.auth_token}`]);
      }
    }catch(err){
      this.promptMsg = 'Error occured while fetching...'
      this.toastr.error('', 'Something went wrong please try again');
    }
  }

}
