import { Component, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { StartupIndiaComponent } from './startup-india.component';
import { ProviderStartupComponent } from './provider-startup/provider-startup.component';
import { HomeComponent } from './home/home.component';

const routes: Routes = [
  { path: '', 
    component: StartupIndiaComponent,
    children:[
      {
        path: '',
        redirectTo: 'startup-india',
        pathMatch:'full'
      },
      {
        path:'startup-india',
        component:HomeComponent
      },
      {
        path: 'startup-india/provider/startup/:auth_token',
        component: ProviderStartupComponent,
      },
    ]
  }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StartupIndiaRoutingModule { }
